<template>
    <div>
        <el-menu
                class="el-menu-vertical"
                @open="handleOpen"
                @close="handleClose"
                router
                :collapse="isCollapse">
            <el-submenu index="1">
                <template slot="title">
                    <i class="el-icon-s-claim"></i>
                    <span slot="title">活动管理</span>
                </template>
                <el-menu-item index="/activity/list"><i class="el-icon-document"></i><span>活动列表</span></el-menu-item>
                <el-menu-item index="/activity/add"><i class="el-icon-plus"></i><span>活动添加</span></el-menu-item>
            </el-submenu>
            <el-submenu index="2">
                <template slot="title">
                    <i class="el-icon-menu"></i>
                    <span slot="title">多多管理</span>
                </template>
                <el-menu-item index="/pdd/cat/goods"><i class="el-icon-document"></i><span>分类商品</span></el-menu-item>
                <el-menu-item index="/pdd/orders"><i class="el-icon-coffee-cup"></i><span>购买订单</span></el-menu-item>
            </el-submenu>
        </el-menu>
    </div>
</template>

<style scoped>
    .el-menu-vertical{
        height: calc(100vh - 49px);
    }
    .el-menu-vertical:not(.el-menu--collapse) {
        width: 200px;
    }

</style>

<script>
    export default {
        data() {
            return {};
        },
        computed:{
            isCollapse(){
                return this.$store.state.system.isCollapse;
            }
        },
        methods: {
            handleOpen(key, keyPath) {
                console.log(key, keyPath);
            },
            handleClose(key, keyPath) {
                console.log(key, keyPath);
            }
        }
    }
</script>


