<template>
    <div class="ls-top-wrap">
        <div class="ls-top-left">
            <div class="ls-top-logo">LARASHOP</div>
            <div class="ls-icon-collapse" @click="toggleCollapse"></div>
        </div>
        <div class="ls-top-right"><div @click="logout" class="logout"><i class="iconfont">&#xe84b; </i>登出</div></div>
    </div>
</template>
<script>
export default {
  methods: {
    toggleCollapse() {
      this.$store.dispatch("toggleState")
    },
    logout(){
        let that = this;
        this.$http
            .post("logout")
            .then(res => {
                if (res.statusCode == "200") {
                    that.$store.commit(that.$types.ACCESS_TOKEN, '');
                    this.$router.push("/login");
                }else if(res.statusCode == "401"){
                    this.$router.push("/login");
                }
            });
    }
  }
}
</script>
<style scoped lang="less">
    .ls-top-wrap{
        padding: 0;
    }
    .ls-top-left{
        float: left;
        width: 200px;
        text-align: right;
        box-sizing: border-box;
    }
    .ls-top-right{
        float: right;
        line-height: 49px;
        cursor: pointer;
        font-size:14px;
        color:darkblue;
        .logout{
            padding-right: 20px;
        }
    }
    .ls-top-logo{
        float: left;
        text-align: left;
        line-height: 49px;
        letter-spacing: .05em;
        padding-left: 30px;
        color:darkblue;
        letter-spacing: 1.5px;
        font-size: 20px;
    }
    .ls-icon-collapse{
        cursor: pointer;
        height: 16px;
        width: 18px;
        background: url('../../css/images/collapse.png') no-repeat;
        float: right;
        margin-top: 16px;
    }
</style>



