<template>
    <div class="ls-container">
        <!--命名视图-->
        <div class="ls-top">
            <backend-top></backend-top>
        </div>
        <div class="ls-content">
            <div class="ls-left">
                <backend-menu></backend-menu>
            </div>
            <!--:class="{'ls-main-collapse': isCollapse}"-->
            <div class="ls-main">
                <div style="padding-top:20px;padding-left: 20px;padding-right: 20px;box-sizing: border-box;">
                    <router-view></router-view>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import top from "@/components/partials/top.vue"
import menu from "@/components/partials/menu.vue"

export default {
    components: {
        "backend-top": top,
        "backend-menu": menu
    },
    computed: {
        isCollapse() {return this.$store.state.system.isCollapse}
    },
    mounted(){
        this.$store.dispatch(this.$types.ACTIVITY_TYPES)
        this.$store.dispatch(this.$types.ACTIVITY_APPLICABLES)
    }
}
</script>
<style scoped lang="less">
    .ls-container{
        margin: 0;
    }
    .ls-top{
        height: 49px;
        background: #fff;
        position: fixed;
        top:0;
        left:0;
        width: 100%;
        border-bottom:1px solid #e0e4e9;
        box-shadow:0 1px 6px rgba(0,0,0,.17);
        z-index: 2;
    }
    .ls-content{
        display: flex;
        flex-direction: row;
        justify-content: space-between;
    }
    .ls-left{
        text-align: left;
        padding-top: 49px;

        position: relative;
    }

    .ls-main{
        margin-top: 49px;

        background: #fff;

        overflow-y: scroll;
        width: 100%;
        height: calc(100vh - 49px);
    }
    .ls-main-collapse{
        width: 100%;
        left: 0;
    }
</style>



